import Vue from 'vue'
import Router from 'vue-router'
import Modulo from '@/components/Modulo'
import Precomp from '@/components/Precomp'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'modulo',
      component: Modulo
    },
  ]
})



