<template>
    <div id="modulo">
        <nav class="nav-extended white">
            <div class="nav-wrapper">
                <a href="http://hinodeasd.it/" class="brand-logo center"><img src="../../static/Logo-Hinode-ASD-1.png" alt=""/></a>
            </div>
            <div class="nav-content">
                <ul class="center">
                    <li @click="hidePdf">
                        <a class="waves-effect waves-light btn-small grey black-text mobile-icons" id="back-btn">
                            <i class="material-icons">arrow_back</i>
                        </a>
                    </li>
                    <li>
                        <a id="print-btn" class="waves-effect waves-light btn-floating btn-large halfway-fab deep-orange lighten-2 black-text mobile-icons" @click="makePdf">
                            <i class="material-icons mobile-icon">print</i></span>
                        </a>
                    </li>
                </ul>
            </div>
        </nav>

        <!--MODULO VISIBILE DA COMPILARE-->
        <div id="visibile" class="container">
            <div class="container row">
                <h4>Modulo d'iscrizione Hinode asd</h4>
                <ul id="form-instructions">
                    <li><h6>1. Compilare in tutte le sue parti</h6></li>
                    <li><h6>2. Cliccare "invia"</h6></li>
                    <li><h6>3. Stampare, firmare e consegnare in cartaceo all'istruttore</h6></li>
                </ul>
                
                <form @submit.prevent="saveSub" class="col s12">
                    <div id="form-container" class="container col s12 card">
                        <h5 id="form-title" class="left">Dati dell'allievo</h5>
                        <div class="input-field col s12">
                            <label>Nome</label>
                            <input type="text" v-model="nome" required>
                        </div>
                        <div class="input-field col s12">
                            <label>Cognome</label>
                            <input type="text" v-model="cognome" required>
                        </div>
                        <div class="input-field col s12">
                                <label>Codice fiscale</label>
                                <input type="text" v-model="cod_fisc" required>
                        </div>
                        <div class="input-field col s12">
                                <label>Luogo di nascita</label>
                                <input type="text" v-model="luogo_nascita" required>
                        </div>
                        <div class="input-field col s12">
                                <label>Data di nascita (GG-MM-AA)</label>
                                <input type="text" v-model="data_nascita" required>
                        </div>
                        <div class="input-field col s12">
                                <label>Residente a</label>
                                <input type="text" v-model="residenza" required>
                        </div>
                        <div class="input-field col s12">
                                <label>CAP</label>
                                <input type="text" v-model="cap" required>
                        </div>
                        <div class="input-field col s12">
                                <label>Indirizzo</label>
                                <input type="text" v-model="indirizzo" required>
                        </div>
                        <div class="input-field col s12">
                                <label>Numero civico</label>
                                <input type="text" v-model="n_civico" required>
                        </div>
                        <h5 id="form-title" class="left">Dati del genitore</h5>
                        <div class="input-field col s12">
                                <label>Nome</label>
                                <input type="text" v-model="nome_genitore" required>
                        </div>
                        <div class="input-field col s12">
                                <label>Cognome</label>
                                <input type="text" v-model="cognome_genitore" required>
                        </div>
                        <div class="input-field col s12">
                                <label>Telefono principale</label>
                                <input type="text" v-model="cell_1" required>
                        </div>
                        <div class="input-field col s12">
                                <label>Email principale</label>
                                <input type="text" v-model="email_1" required>
                        </div>
                        <div class="input-field col s12">
                                <label>Telefono secondario</label>
                                <input type="text" v-model="cell_2" required>
                        </div>
                        <div class="input-field col s12">
                                <label>Email secondario</label>
                                <input type="text" v-model="email_2" required>
                        </div>
                    </div>
                    <input type="submit" value="Invia" class="btn-large deep-orange lighten-1" @click="showPdf">
                </form>
            </div>
        </div>


    <!--MODULO NASCOSTO DA STAMPARE-->
    <div id="precomp">
            <div id="precomp-modulo">
                <div id="bg-image">
                    <img src="../../static/pdf-bg.jpg" alt="">
                </div>
                
                <div id="campi">
                  <div id="campo1" class="campo"> {{ cognome }} </div>
                  <div id="campo2" class="campo"> {{ nome }} </div>
                  <div id="campo3" class="campo"> {{ cod_fisc }} </div>
                  <div id="campo4" class="campo"> {{ luogo_nascita }} </div>
                  <div id="campo5" class="campo small"> {{ data_nascita }} </div>
                  <div id="campo6" class="campo"> {{ residenza }} </div>
                  <div id="campo7" class="campo smaller"> {{ cap }} </div>
                  <div id="campo8" class="campo"> {{ indirizzo }} </div>
                  <div id="campo9" class="campo smaller"> {{ n_civico }} </div>
                  <div id="campo10" class="campo smaller"> {{ cell_1 }} </div>
                  <div id="campo10-2" class="campo small"> {{ email_1 }} </div>
                  <div id="campo11" class="campo smaller"> {{ cell_2 }} </div>
                  <div id="campo11-2" class="campo small"> {{ email_2 }} </div>

                  <div id="campo12" class="campo"> {{ nome_genitore }} {{ cognome_genitore }} </div>
                  <div id="campo13" class="campo"> {{ nome_genitore }} {{ cognome_genitore }} </div>
              </div>
            </div>
          </div>
    </div>
</template>
  


  <script>
    import db from'./firebaseInit'
    import jsPDF from 'jspdf'
    import html2canvas from './html2canvas'
    export default {
    name: 'Modulo',
    data () {
      return {
        nome: null,
        cognome: null,
        cod_fisc: null,            
        luogo_nascita: null,
        data_nascita: null,
        residenza: null,
        cap: null,
        indirizzo: null,
        n_civico: null,
        cell_1: null,
        email_1: null,
        cell_2: null,
        email_2: null,
        nome_genitore: null
      }
    },
    methods: {
        //salva i dati del modulo nel database
        saveSub(){
            db.collection('iscritti2018').add({
                nome: this.nome,
                cognome: this.cognome,
                cod_fisc: this.cod_fisc,
                luogo_nascita: this.luogo_nascita,
                data_nascita: this.data_nascita,
                residenza: this.residenza,
                cap: this.cap,
                indirizzo: this.indirizzo,
                n_civico: this.n_civico,
                cell_1: this.cell_1,
                email_1: this.email_1,
                cell_2: this.cell_2,
                email_2: this.email_2,
                nome_genitore: this.nome_genitore
            }).then()
            .catch(error => console.log(err))
        },
        showPdf(){
            document.getElementById('precomp-modulo').style.transition = '1.5s ease 0.1s'
            document.getElementById('visibile').style.height = '0'
            document.getElementById('visibile').style.opacity = '0'
            document.getElementById('precomp-modulo').style.height = '297mm'
            document.getElementById('precomp-modulo').style.opacity = '1'
            document.querySelectorAll('ul.center')[0].style.display = 'block'
            document.querySelectorAll('ul.center')[1].style.display = 'block'
        },
        makePdf(){
            var options = {
            };
            var pdf = new jsPDF('p', 'pt', 'a4');
            pdf.addHTML($("#precomp-modulo"), 0, 0, options, function() {
            pdf.save('pageContent.pdf');
            });
        },
        hidePdf(){
            document.getElementById('precomp-modulo').style.transition = 'none';
            document.getElementById('visibile').style.opacity = '1'
            document.getElementById('visibile').style.height = 'auto';
            document.getElementById('precomp-modulo').style.height = '0'
            document.getElementById('precomp-modulo').style.opacity = '0'
            document.querySelectorAll('ul.center')[0].style.display = 'none'
            document.querySelectorAll('ul.center')[1].style.display = 'none'
        },
    }
  }
</script>

<style>
    nav{
        padding-top:2vh;
        z-index:1;
        opacity: 0.95;
    }

    .nav-content{
        height: 5vh;
    }

    a.brand-logo img{
        height: 9vh;
        margin: 0vh 0;
    }
    
    ul.center{
        display:none;
    }

    #back-btn{
        position:relative;
        top:-5vh;
        left: 3vw;
    }

    #print-btn{
        right: 4vw;
    }

    #form-instructions{
        margin-top:5vh;
        margin-bottom: 5vh;
    }

    #form-container{
        padding:4vh 5vw;
        margin-bottom: 4vh;
    }

    #visibile{
        overflow:hidden;
        margin-top: 18vh;
    }

    #precomp-modulo{
        width: 210mm; 
        height: 0;
        opacity:0;
        overflow: hidden;
        margin: 0 auto;
        margin-top: 3vh;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }
      
    #bg-img{
          z-index: -1;
    }
      
    #campi{
        position: relative;
        top: -298mm
    }
      
    .campo{
          width:35%;
          height:20px;
    }
      
    .small{
          width:15%;
    }
      
    .smaller{
          width:10%;
    }
      
    .big{
          width:65%;
    }
      
    #campo1{
          margin: 7.5% 0 0 15%;
          float:left;
    }
      
    #campo2{
        margin: 7.5% 0 0 9%;
        float:left;
    }
      
    #campo3{
        margin: 1.21% 0 0 25%;
        float:left;
    }
      
    #campo4{
        margin: 1.21% 0 0 25%;
        float:left;
    }
      
    #campo5{
        margin: 1.21% 0 0 18%;
        float:left;
    }
      
    #campo6{
        margin: 1.21% 0 0 25%;
        float:left;
    }
      
    #campo7{
        margin: 1.21% 0 0 23%;
        float:left;
    }
      
    #campo8{
        margin: 1.21% 0 0 25%;
        float:left;
    }
      
    #campo9{
        margin: 1.21% 0 0 24%;
        float:left;
    }
      
    #campo10{
        margin: 1.21% 0 0 25%;
        float:left;
    }

    #campo10-2{
        margin: 1.21% 0 0 25%;
        float:left;
    }
      
    #campo11{
        margin: 1.21% 0 0 25%;
        float:left;
    }

    #campo11-2{
        margin: 1.21% 0 0 25%;
        float:left;
    }

    #campo12{
        margin: 15% 0 0 40%;
        float:left;
    }

    #campo13{
        margin: 4% 0 0 40%;
        float:left;
    }

    @media(min-width: 960px){
        nav{
        height: 11vh;
        padding: 1vh 28vw;
        }

        #back-btn{
            top:-3vh;
        }
    }

    @media(max-width: 960px){
        html, body{
            width:100vw;
            overflow:hidden;
        }
    /* #precomp-modulo{
            overflow: scroll;
        } */
    }    
</style>


